*{
  padding:0;
  margin:0;
  box-sizing: border-box;
  -webkit-user-drag: none;
  
}

body{
  background-color:rgb(33, 100, 117);
  display:grid;
  place-content: center;
  font-family:"Roboto, sans-serif";
  height:100vh;
  font-size:2rem;

}
.container{
  height:80vh;
  width:50vh;
  background-color:black;
  border-radius: 1rem;
  padding:1rem;
  -webkit-box-shadow:7px 7px 5px 0px rgba(50,50,50,0.75);
  -moz-box-shadow:7px 7px 5px 0px rgba(50,50,50,0.75);
  box-shadow:7px 7px 5px 0px rgba(50,50,50,0.75);

}
.wrapper{
  display:grid;
  height:100%;
  width:100%;
  grid-template-columns:repeat(4,1fr);
  grid-template-rows: repeat(6,1fr);
  gap:10px;

}
.screen{
  grid-column: 1/ -1;
  color:white;
  text-align:right;
  padding:0.5rem;
  font-size:2.5rem;
  overflow:hidden;
  border:2px solid rgb(3, 112, 142);
  border-radius:10px;
}
.btn{
  display: grid;
  place-content: center;
  background-color:rgb(54,52,52);
  border-radius: 20%;
  color: white;
  cursor: pointer;
  font-weight:bold;
}

.btn:hover{
  background-color:rgb(163, 160, 160);
  color:rgb(192, 12, 12);

}
.light-gray{
  background-color:rgb(152, 156, 156);
  color:black;
}
.light-gray:hover{
  background-color:white;

}

.orange{
  background-color:rgb(10, 109, 109);
}
.orange:hover{
  background-color:white;
  color: orangered;
}

.equal{
  background-color:rgb(10, 109, 109);
}